import React from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";
function Navbar() {
  const navStyle = {
    color: "white",
    textDecoration: "none",
  };

  return (
    <>
      <header>
        <div className="logo">TRVL</div>
        <div className="links">
          <ul>
            <Link style={navStyle} to="/">
              <li>Home</li>
            </Link>
            <Link style={navStyle} to="/services">
              <li>Services</li>
            </Link>
            <Link style={navStyle} to="/product">
              <li>Products</li>
            </Link>
          </ul>
          <Link style={navStyle} to="/signup">
            <button>SIGN UP</button>
          </Link>
        </div>
      </header>
    </>
  );
}

export default Navbar;
